import React from "react";
import "./css/global.css";
import saveImg from "./assets/save.svg";
import myFace from "./assets/face.jpg";
import Block from "./components/Block";
import {
  profileText,
  jobExperienceTitle,
  jobExperienceData,
  educationData,
  referenceData,
  cursosData,
  personalData,
} from "./Constants";
import Habilities from "./components/Habilities";

function App() {
  const print = () => {
    window.print();
  };

  return (
    <div className="App">
      <header>
        <img className="face" src={myFace} alt="face" />
        <div className="name-container">
          <h1>Juan David Nuñez Aguilar</h1>
          <p>Desarrollador Web</p>
        </div>
        <div className="btn-container">
          <button className="btn-download" onClick={print}>
            <img src={saveImg} alt="guardar" />
          </button>
        </div>
      </header>
      <section>
        <Block title="Perfil" text={profileText} data={[]} />
        <Habilities />
        <Block
          title={jobExperienceTitle}
          data={jobExperienceData}
          break={true}
        />
        <Block title="Educación" data={educationData} />
        <Block title="Referencias" data={referenceData} />
        <Block title="Cursos adicionales" data={cursosData} />
        <Block title="Datos personales" data={personalData} />
      </section>
    </div>
  );
}

export default App;
