import React from "react";

const Progress = (props) => {
  let width = `width: ${props.value}`;
  return (
    <div className="progress-container">
      <h4>{props.title}</h4>
      <div className="progress-bar">
        <div className="filled" style={{ width: props.value + "%" }}></div>
      </div>
    </div>
  );
};

export default Progress;
